.containerCompetencePoker{
    width: 80%;
    margin: 0 auto;
    margin-bottom: 150px;
}

.englobeTitreCompetencePoker{
    width: 100%;
    height: 100px;
    margin-bottom: 150px;
}

.titreCompetencePoker{
    width: 100%;
    height: 100%;
    background-color: #005779;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 35px;
    font-weight: bold;
}
.titreCompetencePoker p{
 height: 100%;
 display: flex;
 align-items: center;
 justify-content: center;
}

.englobeAllCompetence{
    width: 100%;
    height: 800px;
    
}

.allCompetences{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border: #005779 1px solid;
    opacity: 0;
}

.allCompetencesVisible{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border: #005779 1px solid;
    opacity: 1;
}

.partieBulleCompetence{
    width: 45%;
    height: 80%;
    display: flex;
    flex-direction: column;
}

.traitMilieuCompetence{
    width: 20px;
    height: 80%;
   background-color: #005779;
   position: relative;
   overflow: hidden;
}

.traitMilieuCompetenceDessus{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0%;
    left: 0%;
    background-color: white;
}

.GraphiqueCompetence{
    width: 45%;
    height: 80%;
}

.premiereBarreGraphique,.DeuxiemeBarreGraphique,.TroisiemeBarreGraphique,.QuatriemeBarreGraphique,.CinquiemeBarreGraphique{
    width: 100%;
    height: 20%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.titreDugraphique{
    height: 30%;
    width: 30%;
    font-size: 25px;
    text-align: center;
    line-height: 35px;
}

.titreDugraphiqueDeux{
    height: 100%;
    width: 100%;
    color: white;
    background-color: #005779;
}

.barreDuGraphique{
    height: 30%;
    width: 60%;
    border: 1px solid #005779;
    position: relative;
    font-size: 25px;
    text-align: right;
    line-height: 35px;
    overflow: hidden;
}

.barreDuGraphiqueInvisible{
    position: absolute;
    height: 100%;
    top: 0%;
    left: -1%;
    width: 1%;
    background-color: #005779;
}

.enteteCompetence,.milieuCompetence,.finCompetence{
    width: 100%;
    height: 33%;
}

.milieuCompetence{
    display: flex;
    align-items: center;
    justify-content: space-around;
}


.enteteCompetence,.finCompetence{
    display: flex;
    align-items: center;
    justify-content: center;
}


.premiereCompetence,.deuxiemeCompetence,.troisiemeCompetence,.quatriemeCompetence,.cinquiemeCompetence{
    width: 200px;
    height: 150px;
    position: relative;
}

.eUnpremiereCompetence,#bulleAdaptation,#bulleMotivation{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0%;
    left: 0%;
    background-color: white;
    color: #005779;
    border: #005779 3px solid;
    border-radius: 90%;
    font-size: 25px;
    transform: rotateY(270deg);
}

.eDeuxpremiereCompetence,#bullePatience,#bulleIntelligence{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0%;
    left: 0%;
    transform: rotateY(270deg);
    background-color: #005779;
    border: none;
    color: white;
    border-radius: 90%;
    font-size: 25px;
}

.invisible{
    opacity:0;
}

.eDeuxpremiereCompetence:hover,.eUnpremiereCompetence:hover,#bulleAdaptation,#bullePatience,#bulleMotivation,#bulleIntelligence{
    cursor: pointer;
}

.rotationDeBaseDeux{
    transform: rotateY(0deg);
}

.rotationDeBase{
    transform: rotateY(270deg);
}

.premiereRotations{
    animation: premiereRotation 0.5s linear forwards;
}

@keyframes premiereRotation{
    0%{  transform: rotateY(0deg);}
    100%{  transform: rotateY(90deg);}
}

.deuxiemeRotations{
    animation: deuxiemeRotation 0.5s linear forwards;
}

@keyframes deuxiemeRotation{
    0%{  transform: rotateY(270deg);}
    100%{  transform: rotateY(360deg);}
}

.visible{
    animation: visibles 0.5s linear forwards;
}

@keyframes visibles {
    0%{opacity:0;}
    100%{opacity:1;}
}

.cinqPourCent{
    animation: augmentationCinq 0.5s linear forwards;
}

@keyframes augmentationCinq {
    0%{width: 1%;}
    100%{width: 5%;}
}

.MoinsCinqPourCent{
    animation: diminutionCinq 0.5s linear forwards;
}

@keyframes diminutionCinq {
    0%{width: 5%;}
    100%{width: 1%;}
}


.dixPourCent{
    animation: augmentationDix 0.5s linear forwards;
}

@keyframes augmentationDix {
    0%{width: 1%;}
    100%{width: 10%;}
}

.MoinsdixPourCent{
    animation: diminutionationDix 0.5s linear forwards;
}

@keyframes diminutionationDix {
    0%{width: 10%;}
    100%{width: 1%;}
}

.OnzePourCent{
    animation: augmentationOnze 0.5s linear forwards;
}

@keyframes augmentationOnze {
    0%{width: 1%;}
    100%{width: 11%;}
}

.diminutionOnzePourCent{
    animation: diminutionOnze 0.5s linear forwards;
}

@keyframes diminutionOnze {
    0%{width: 11%;}
    100%{width: 1%;}
}

.DouzePourCent{
    animation: augmentationDouze 0.5s linear forwards;
}

@keyframes augmentationDouze {
    0%{width: 1%;}
    100%{width: 12%;}
}

.diminutionDouzePourCent{
    animation: diminutionDouze 0.5s linear forwards;
}

@keyframes diminutionDouze {
    0%{width: 12%;}
    100%{width: 1%;}
}

.treizePourCent{
    animation: augmentationTreize 0.5s linear forwards;
}

@keyframes augmentationTreize {
    0%{width: 1%;}
    100%{width: 13%;}
}

.diminutionTreizePourCent{
    animation: diminutionTreize 0.5s linear forwards;
}

@keyframes diminutionTreize {
    0%{width: 13%;}
    100%{width: 1%;}
}

.quinzePourCent{
    animation: augmentationQuinze 0.5s linear forwards;
}

@keyframes augmentationQuinze {
    0%{width: 1%;}
    100%{width: 15%;}
}

.MoinsquinzePourCent{
    animation: diminutionQuinze 0.5s linear forwards;
}

@keyframes diminutionQuinze {
    0%{width: 15%;}
    100%{width: 1%;}
}

.cacherParaTitreCompetences{
    animation: cacherParaTitreCompetence 0.5s linear forwards;
}

@keyframes cacherParaTitreCompetence {
    0%{opacity: 1;}
    100%{opacity: 0;}
}

.afficherParaTitreCompetencesUn{
    animation: afficherParaTitreCompetence 0.5s linear forwards;
}

@keyframes afficherParaTitreCompetence  {
    0%{opacity: 0;}
    100%{opacity: 1;}
}

.premiereTransformation{
    background-color: white;
    color: #005779;
    border: solid 2px #005779;
    height: 100%;
    width: 100%;
    opacity: 1;
}
.deuxiemeTransformation{
 
    background-color: #005779;
    color: white;
    height: 100%;
    width: 100%;
}

.apparitionTraitMilieu{
    animation: apparitionDuTraitMilieu 1s linear forwards;
}

@keyframes apparitionDuTraitMilieu {
    0%{transform: translateY(0%);}
    100%{transform: translateY(-100%);}
}

/* Partie responsive */
@media (max-width: 1812px) {
    .GraphiqueCompetence{
        width: 30%;
    }
    .partieBulleCompetence{
        width: 60%;
    }
    .titreDugraphique{
        width: 40%;
    }
    .barreDuGraphique{
        width: 40%;
    }
    .containerCompetencePoker{
        width: 100%;
    }
    .englobeAllCompetence{
        height: 700px;
    }
}

@media(max-width :1150px ){
    .traitMilieuCompetence{
        display: none;
    }
    .allCompetencesVisible{
        display: flex;
        flex-direction:column;
        align-items: center;
        justify-content: space-between;
    }

    .eUnpremiereCompetence,#bulleAdaptation,#bullePatience,#bulleMotivation,#bulleIntelligence{
       width: 80%;
       height: 60%;
       font-size: 20px;
    }
    
    .eDeuxpremiereCompetence{
        width: 80%;
        height: 60%;
        font-size: 20px;
    }
    .partieBulleCompetence{
        height: 65%;
        width: 80%;
      }
    .GraphiqueCompetence{
        width: 60%;
        height: 100%;
    }
    .titreDugraphique,.barreDuGraphique{
        height: 80%;
    }
}

@media (max-width: 920px) {
    .titreCompetencePoker{
    font-size: 25px;
    width:100%;
  }
  .partieBulleCompetence{
    width: 100%;
  }
  .GraphiqueCompetence{
    width: 80%;
    
    }
}

@media (max-width: 638px) {
    .titreCompetencePoker{
      font-size: 20px;
    }
}

@media (max-width: 541px) {
    .eUnpremiereCompetence,#bulleAdaptation,#bullePatience,#bulleMotivation,#bulleIntelligence{
        width: 100px;
        height: 80px;
        font-size: 12px;
     }
     .GraphiqueCompetence{
        width: 100%;
        
        }
     
     .eDeuxpremiereCompetence{
        width: 100px;
        height: 80px;
         font-size: 12px;
     }

     #bulleAdaptation,#bullePatience,#bulleMotivation,#bulleIntelligence{
        left: 10%;
     }
    
}

@media (max-width: 479px) {
    

     #bulleAdaptation,#bullePatience,#bulleMotivation,#bulleIntelligence{
        left: 14%;
     }
    
}

@media (max-width: 456px) {
    

    #bulleAdaptation,#bullePatience,#bulleMotivation,#bulleIntelligence{
       left: 16%;
    }
   
}


@media (max-width: 420px) {
    

    #bulleAdaptation,#bullePatience,#bulleMotivation,#bulleIntelligence{
       left: 19%;
    }
   
}